import Person from './Persons';
import { getDATA } from '../service';


class Employee extends Person {
   constructor(name, surname, department, salary) {
    super(name,surname);
    this.name = name;
    this.surname = surname;
    this.department = department;
    this.salary = salary;
   };
   static fromJSON(obj) {
      return Object.assign(new Person(), obj)
   };
};

export default Employee;

export function jsonToEmployees(employeesJSON) {
   let employees = [];
   for (let e of employeesJSON) {
    employees.push(Employee.fromJSON(e));
   };
   return employees;
};

window.allEmployees = function() {
   return jsonToEmployees(newData);
 } 